import React, { useState, useEffect } from 'react';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { QuotationData, ServiceItem } from '../types';
import ServiceItemForm from './ServiceItemForm';
import QuotationModal from './QuotationModal';

const QuotationGenerator: React.FC = () => {
  const [formData, setFormData] = useState<QuotationData>({
    quotationName: '',
    company: '',
    quoterName: '',
    email: '',
    serviceItems: [
      { category: '', item: '', price: 0, count: 1, unit: '', amount: 0 }
    ],
    excludingTax: 0,
    percentage: 5,
    tax: 0,
    includingTax: 0,
    isSign: true
  });

  const [logoFile, setLogoFile] = useState<string>('');
  const [showModal, setShowModal] = useState(false);
  const [isPreview, setIsPreview] = useState(false);
  const [showLogoPreview, setShowLogoPreview] = useState(false);
  const [historyData, setHistoryData] = useState<QuotationData[]>([]);
  const [emailError, setEmailError] = useState<string>('');
  const [pasteMode, setPasteMode] = useState<'replace' | 'append'>('replace');
  const [pasteText, setPasteText] = useState<string>('');

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  useEffect(() => {
    const saved = localStorage.getItem('quotation');
    if (saved) {
      setHistoryData(JSON.parse(saved));
    }
  }, []);

  useEffect(() => {
    const total = formData.serviceItems.reduce((acc, item) => acc + item.amount, 0);
    const tax = Math.ceil((formData.percentage / 100) * total);
    setFormData(prev => ({
      ...prev,
      excludingTax: total,
      tax,
      includingTax: total + tax
    }));
  }, [formData.serviceItems, formData.percentage]);

  const handleInputChange = (field: keyof QuotationData, value: any) => {
    setFormData(prev => ({ ...prev, [field]: value }));

    // Email 
    if (field === 'email') {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (value && !emailRegex.test(value)) {
        setEmailError('');
      } else {
        setEmailError('');
      }
    }
  };

  const handleLogoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setLogoFile(URL.createObjectURL(file));
    }
  };

  const handleServiceItemChange = (index: number, item: ServiceItem) => {
    const newItems = [...formData.serviceItems];
    newItems[index] = item;
    setFormData(prev => ({ ...prev, serviceItems: newItems }));
  };

  const addServiceItem = () => {
    setFormData(prev => ({
      ...prev,
      serviceItems: [
        ...prev.serviceItems,
        { category: '', item: '', price: 0, count: 1, unit: '', amount: 0 }
      ]
    }));
  };

  const removeServiceItem = (index: number) => {
    if (formData.serviceItems.length > 1) {
      const newItems = formData.serviceItems.filter((_, i) => i !== index);
      setFormData(prev => ({ ...prev, serviceItems: newItems }));
    }
  };

  const handleHistoryChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const index = parseInt(e.target.value);
    if (!isNaN(index) && historyData[index]) {
      setFormData(historyData[index]);
    }
  };

  const saveToHistory = (data: QuotationData) => {
    const dataWithTimestamp = {
      ...data,
      createdAt: new Date().toISOString()
    };
    const newHistory = [dataWithTimestamp, ...historyData.slice(0, 4)];
    setHistoryData(newHistory);
    localStorage.setItem('quotation', JSON.stringify(newHistory));
  };

  const handleSubmit = () => {
    saveToHistory(formData);
    setIsPreview(false);
    setShowModal(true);
  };

  const handlePreview = () => {
    setIsPreview(true);
    setShowModal(true);
  };

  const handleExportAllData = () => {
    const exportData = {
      currentData: formData,
      historyData: historyData,
      exportDate: new Date().toISOString()
    };

    const dataStr = JSON.stringify(exportData, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });

    const link = document.createElement('a');
    link.href = URL.createObjectURL(dataBlob);

    //  PDF 
    const dateString = formData.startDate || new Date().toISOString().split('T')[0];
    const fileName = formData.quotationName
      ? `${formData.quotationName}__${dateString}.json`
      : `${new Date().toLocaleDateString('zh-TW')}_quotation_.json`;

    link.download = fileName;
    link.click();
  };

  const handleExportCurrentData = () => {
    const exportData = {
      currentData: formData,
      exportDate: new Date().toISOString()
    };

    const dataStr = JSON.stringify(exportData, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });

    const link = document.createElement('a');
    link.href = URL.createObjectURL(dataBlob);

    //  PDF 
    const dateString = formData.startDate || new Date().toISOString().split('T')[0];
    const fileName = formData.quotationName
      ? `${formData.quotationName}_${dateString}.json`
      : `${new Date().toLocaleDateString('zh-TW')}_quotation.json`;

    link.download = fileName;
    link.click();
  };

  const handleImportData = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const importedData = JSON.parse(e.target?.result as string);
        let importedCount = 0;

        // 
        if (importedData.currentData) {
          setFormData(importedData.currentData);
          importedCount++;
        }

        // 
        if (importedData.historyData && Array.isArray(importedData.historyData)) {
          // 
          const existingIds = new Set(historyData.map(item => JSON.stringify(item)));
          const newHistoryItems = importedData.historyData.filter(
            (item: any) => !existingIds.has(JSON.stringify(item))
          );

          if (newHistoryItems.length > 0) {
            const mergedHistory = [...newHistoryItems, ...historyData].slice(0, 5);
            setHistoryData(mergedHistory);
            localStorage.setItem('quotation', JSON.stringify(mergedHistory));
            importedCount += newHistoryItems.length;
          }
        }

        if (importedCount > 0) {
          alert(` ${importedCount} `);
        } else {
          alert('');
        }
      } catch (error) {
        alert(' JSON ');
      }
    };

    reader.readAsText(file);
    //  input 
    event.target.value = '';
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;

    if (over && active.id !== over.id) {
      const oldIndex = formData.serviceItems.findIndex((_, index) => `item-${index}` === active.id);
      const newIndex = formData.serviceItems.findIndex((_, index) => `item-${index}` === over.id);

      const newServiceItems = arrayMove(formData.serviceItems, oldIndex, newIndex);
      setFormData(prev => ({ ...prev, serviceItems: newServiceItems }));
    }
  };

  const handlePasteText = (text: string) => {
    if (!text.trim()) {
      return;
    }

    const lines = text.trim().split('\n').filter(line => line.trim());

    if (lines.length === 0) {
      return;
    }

    // 
    const parsedItems: ServiceItem[] = [];
    let startIndex = 0;

    // 
    const firstLine = lines[0].split(/\s{2,}|\t/).map(cell => cell.trim());
    const isHeaderRow = firstLine.some(cell =>
      ['', '', '', '', '', ''].includes(cell)
    );

    if (isHeaderRow) {
      startIndex = 1;
    }

    // 
    for (let i = startIndex; i < lines.length; i++) {
      const line = lines[i];

      // Tab
      let cells: string[] = [];

      //  Tab 
      if (line.includes('\t')) {
        cells = line.split('\t').map(cell => cell.trim());
      } else {
        // 
        cells = line.split(/\s{2,}/).map(cell => cell.trim());

        // 
        if (cells.length === 1 && line.includes(' ')) {
          cells = line.split(' ').map(cell => cell.trim()).filter(cell => cell);
        }
      }

      if (cells.length >= 4) { // 
        const category = cells[0] || '';
        const item = cells[1] || '';
        const price = parseFloat(cells[2]) || 0;
        const count = parseInt(cells[3]) || 1;
        const unit = cells[4] || '';
        const amount = cells.length >= 6 ? parseFloat(cells[5]) || 0 : price * count;

        // 
        if (category && item) {
          parsedItems.push({
            category,
            item,
            price,
            count,
            unit,
            amount
          });
        }
      }
    }

    if (parsedItems.length > 0) {
      setFormData(prev => ({
        ...prev,
        serviceItems: pasteMode === 'replace' ? parsedItems : [...prev.serviceItems, ...parsedItems]
      }));
      // 
    }
    // 
  };

  const handlePasteFromInput = () => {
    if (!pasteText.trim()) {
      return; // 
    }

    const lines = pasteText.trim().split('\n').filter(line => line.trim());

    if (lines.length === 0) {
      return;
    }

    // 
    const parsedItems: ServiceItem[] = [];
    let startIndex = 0;

    // 
    const firstLine = lines[0].split(/\s{2,}|\t/).map(cell => cell.trim());
    const isHeaderRow = firstLine.some(cell =>
      ['', '', '', '', '', ''].includes(cell)
    );

    if (isHeaderRow) {
      startIndex = 1;
    }

    // 
    for (let i = startIndex; i < lines.length; i++) {
      const line = lines[i];

      // Tab
      let cells: string[] = [];

      //  Tab 
      if (line.includes('\t')) {
        cells = line.split('\t').map(cell => cell.trim());
      } else {
        // 
        cells = line.split(/\s{2,}/).map(cell => cell.trim());

        // 
        if (cells.length === 1 && line.includes(' ')) {
          cells = line.split(' ').map(cell => cell.trim()).filter(cell => cell);
        }
      }

      if (cells.length >= 4) { // 
        const category = cells[0] || '';
        const item = cells[1] || '';
        const price = parseFloat(cells[2]) || 0;
        const count = parseInt(cells[3]) || 1;
        const unit = cells[4] || '';
        const amount = cells.length >= 6 ? parseFloat(cells[5]) || 0 : price * count;

        // 
        if (category && item) {
          parsedItems.push({
            category,
            item,
            price,
            count,
            unit,
            amount
          });
        }
      }
    }

    if (parsedItems.length > 0) {
      setFormData(prev => ({
        ...prev,
        serviceItems: pasteMode === 'replace' ? parsedItems : [...prev.serviceItems, ...parsedItems]
      }));
      // 
      setPasteText(''); // 
    }
    // 
  };

  const handleCopyToClipboard = async () => {
    try {
      const headers = ['', '', '', '', '', ''];
      const rows = [
        headers.join('\t'),
        ...formData.serviceItems.map(item =>
          [item.category, item.item, item.price, item.count, item.unit, item.amount].join('\t')
        )
      ];

      const text = rows.join('\n');
      await navigator.clipboard.writeText(text);
      alert('');
    } catch (error) {
      alert('');
    }
  };

  const isFormValid = formData.company && formData.quoterName && formData.email &&
    !emailError && formData.serviceItems.some(item => item.item && item.price > 0);

  return (
    <div className="container mt-4">
      <div className="page-header mb-4">
        <div className="d-flex justify-content-between align-items-center">
          <h1 className="h2"></h1>
          <div className="d-flex gap-2">
            <div className="btn-group">
              <button
                type="button"
                className="btn btn-outline-secondary btn-sm"
                onClick={handleExportCurrentData}
              >
                
              </button>
              <button
                type="button"
                className="btn btn-outline-secondary btn-sm"
                onClick={handleExportAllData}
              >
                
              </button>
            </div>
            <label className="btn btn-outline-secondary btn-sm mb-0">
              
              <input
                type="file"
                accept=".json"
                onChange={handleImportData}
                style={{ display: 'none' }}
              />
            </label>
          </div>
        </div>
      </div>

      <form>
        <div className="card mb-4">
          <div className="card-body">
            {historyData.length > 0 && (
              <div className="row mb-3">
                <div className="col-md-6 offset-md-6">
                  <label className="form-label"></label>
                  <select
                    className="form-select"
                    onChange={handleHistoryChange}
                    defaultValue=""
                  >
                    <option value=""></option>
                    {historyData.map((data, index) => {
                      let displayName = '';

                      if (data.quotationName) {
                        if (data.createdAt) {
                          const createdDate = new Date(data.createdAt);
                          const dateTimeString = createdDate.toLocaleString('zh-TW', {
                            year: 'numeric',
                            month: '2-digit',
                            day: '2-digit',
                            hour: '2-digit',
                            minute: '2-digit'
                          });
                          displayName = `${data.quotationName} (${dateTimeString})`;
                        } else {
                          displayName = data.quotationName;
                        }
                      } else {
                        displayName = `${data.startDate || ''} - ${data.company}`;
                      }

                      return (
                        <option key={index} value={index}>
                          {displayName}
                        </option>
                      );
                    })}
                  </select>
                </div>
              </div>
            )}

            <h3 className="text-muted mb-3"></h3>

            <div className="mb-3">
              <label className="form-label"></label>
              <input
                type="text"
                className="form-control"
                value={formData.quotationName}
                onChange={(e) => handleInputChange('quotationName', e.target.value)}
                placeholder=""
              />
            </div>

            <div className="mb-3">
              <label className="form-label"> LOGO</label>
              <div className="d-flex gap-2">
                <input
                  type="file"
                  className="form-control"
                  accept="image/*"
                  onChange={handleLogoChange}
                />
                {logoFile && (
                  <button
                    type="button"
                    className="btn btn-outline-secondary"
                    onClick={() => setShowLogoPreview(true)}
                    style={{
                      height: '52px',
                      minWidth: '70px',
                      fontSize: '14px',
                      padding: '0 12px'
                    }}
                  >
                    
                  </button>
                )}
              </div>
            </div>

            <div className="row mb-3">
              <div className="col-md-6">
                <label className="form-label"> <span className="text-danger">*</span></label>
                <input
                  type="text"
                  className="form-control"
                  value={formData.company}
                  onChange={(e) => handleInputChange('company', e.target.value)}
                  placeholder=""
                  required
                />
              </div>
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  type="text"
                  className="form-control"
                  value={formData.customerTaxID || ''}
                  onChange={(e) => handleInputChange('customerTaxID', e.target.value)}
                  placeholder=""
                  pattern="[0-9]{8}"
                  maxLength={8}
                />
              </div>
            </div>

            <div className="row mb-3">
              <div className="col-md-6">
                <label className="form-label">/ <span className="text-danger">*</span></label>
                <input
                  type="text"
                  className="form-control"
                  value={formData.quoterName}
                  onChange={(e) => handleInputChange('quoterName', e.target.value)}
                  placeholder=""
                  required
                />
              </div>
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  type="text"
                  className="form-control"
                  value={formData.quoterTaxID || ''}
                  onChange={(e) => handleInputChange('quoterTaxID', e.target.value)}
                  placeholder=""
                  pattern="[0-9]{8}"
                  maxLength={8}
                />
              </div>
            </div>

            <div className="row mb-3">
              <div className="col-md-6">
                <label className="form-label">Email <span className="text-danger">*</span></label>
                <input
                  type="email"
                  className={`form-control ${emailError ? 'is-invalid' : ''}`}
                  value={formData.email}
                  onChange={(e) => handleInputChange('email', e.target.value)}
                  placeholder=""
                  required
                />
                {emailError && (
                  <div className="invalid-feedback">
                    {emailError}
                  </div>
                )}
              </div>
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  type="tel"
                  className="form-control"
                  value={formData.tel || ''}
                  onChange={(e) => handleInputChange('tel', e.target.value)}
                  placeholder=""
                />
              </div>
            </div>

            <div className="row mb-3">
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  type="date"
                  className="form-control"
                  value={formData.startDate || ''}
                  onChange={(e) => handleInputChange('startDate', e.target.value)}
                />
              </div>
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  type="date"
                  className="form-control"
                  value={formData.endDate || ''}
                  onChange={(e) => handleInputChange('endDate', e.target.value)}
                />
              </div>
            </div>
          </div>
        </div>

        <div className="card mb-4">
          <div className="card-body">
            <div className="d-flex justify-content-between align-items-center mb-3">
              <h3 className="text-muted"></h3>
              <div className="d-flex gap-2 align-items-center">
                <div className="form-check form-check-inline me-2">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="pasteMode"
                    id="pasteModeReplace"
                    value="replace"
                    checked={pasteMode === 'replace'}
                    onChange={(e) => setPasteMode(e.target.value as 'replace' | 'append')}
                  />
                  <label className="form-check-label small" htmlFor="pasteModeReplace">
                    
                  </label>
                </div>
                <div className="form-check form-check-inline me-3">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="pasteMode"
                    id="pasteModeAppend"
                    value="append"
                    checked={pasteMode === 'append'}
                    onChange={(e) => setPasteMode(e.target.value as 'replace' | 'append')}
                  />
                  <label className="form-check-label small" htmlFor="pasteModeAppend">
                    
                  </label>
                </div>

                <button
                  type="button"
                  className="btn btn-secondary btn-sm"
                  onClick={handleCopyToClipboard}
                  title=""
                  style={{ backgroundColor: '#28a745', color: 'white', borderColor: '#28a745' }}
                >
                    Excel / Google Sheet
                </button>
                <button
                  type="button"
                  className="btn btn-secondary btn-sm"
                  onClick={() => {
                    if (window.confirm('')) {
                      setFormData(prev => ({
                        ...prev,
                        serviceItems: [{ category: '', item: '', price: 0, count: 1, unit: '', amount: 0 }]
                      }));
                    }
                  }}
                  title=""
                  style={{ backgroundColor: '#6c757d', color: 'white', borderColor: '#6c757d' }}
                >
                   
                </button>
                <button
                  type="button"
                  className="btn btn-primary btn-sm"
                  onClick={addServiceItem}
                >
                  
                </button>
              </div>
            </div>



            <div className="alert alert-info mb-3">
              <small>
                <strong> </strong><br />
                  Excel/Google Sheets <br />
                 Tab <br />
                 <br />
                 <br />
                 <strong></strong><br />
                 <strong></strong>
              </small>
            </div>

            <div className="mb-3">
              <div className="input-group">
                <span className="input-group-text"></span>
                <textarea
                  className="form-control"
                  rows={3}
                  placeholder=" Excel  Google Sheets ..."
                  value={pasteText}
                  onChange={(e) => setPasteText(e.target.value)}
                  onPaste={(e) => {
                    // 
                    e.preventDefault();

                    // 
                    const clipboardData = e.clipboardData;
                    const pastedText = clipboardData.getData('text');

                    // 
                    handlePasteText(pastedText);
                  }}
                />
              </div>
            </div>

            <DndContext
              sensors={sensors}
              collisionDetection={closestCenter}
              onDragEnd={handleDragEnd}
            >
              <SortableContext
                items={formData.serviceItems.map((_, index) => `item-${index}`)}
                strategy={verticalListSortingStrategy}
              >
                {formData.serviceItems.map((item, index) => (
                  <ServiceItemForm
                    key={`item-${index}`}
                    id={`item-${index}`}
                    item={item}
                    onChange={(newItem) => handleServiceItemChange(index, newItem)}
                    onRemove={() => removeServiceItem(index)}
                    canRemove={formData.serviceItems.length > 1}
                    showHeaders={index === 0}
                  />
                ))}
              </SortableContext>
            </DndContext>

            <div className="text-end fs-5 mt-3">
               NT$ <span className="fw-bold text-danger">{formData.excludingTax}</span> 
            </div>
          </div>
        </div>

        <div className="card mb-4">
          <div className="card-body">
            <h3 className="text-muted mb-3"></h3>

            <div className="row g-3 mb-2">
              <div className="col-md-4">
                <label className="form-label small fw-bold text-muted"></label>
              </div>
              <div className="col-md-4">
                <label className="form-label small fw-bold text-muted"></label>
              </div>
              <div className="col-md-4">
                <label className="form-label small fw-bold text-muted"></label>
              </div>
            </div>

            <div className="row g-3">
              <div className="col-md-4">
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  value={formData.taxName || ''}
                  onChange={(e) => handleInputChange('taxName', e.target.value)}
                />
              </div>
              <div className="col-md-4">
                <div className="input-group">
                  <input
                    type="number"
                    className="form-control"
                    placeholder=""
                    value={formData.percentage}
                    onChange={(e) => handleInputChange('percentage', parseFloat(e.target.value) || 0)}
                  />
                  <span className="input-group-text">%</span>
                </div>
              </div>
              <div className="col-md-4">
                <input
                  type="number"
                  className="form-control"
                  placeholder=""
                  value={formData.tax}
                  readOnly
                />
              </div>
            </div>
            <div className="text-end fs-5 mt-3">
               NT$ <span className="fw-bold text-danger">{formData.includingTax}</span> 
            </div>
          </div>
        </div>

        <div className="card mb-4">
          <div className="card-body">
            <div className="mb-3">
              <label className="form-label"></label>
              <textarea
                className="form-control"
                rows={3}
                value={formData.desc || ''}
                onChange={(e) => handleInputChange('desc', e.target.value)}
                placeholder="..."
              />
            </div>
            <div className="form-check">
              <input
                type="checkbox"
                className="form-check-input"
                id="isSign"
                checked={formData.isSign}
                onChange={(e) => handleInputChange('isSign', e.target.checked)}
              />
              <label className="form-check-label" htmlFor="isSign">
                
              </label>
            </div>
          </div>
          <div className="card-footer">
            <div className="d-flex gap-2 justify-content-end">
              <button
                type="button"
                className="btn btn-primary"
                onClick={handleSubmit}
                disabled={!isFormValid}
              >
                
              </button>
            </div>
          </div>
        </div>
      </form>

      {showModal && (
        <QuotationModal
          data={formData}
          logo={logoFile}
          isPreview={isPreview}
          onClose={() => setShowModal(false)}
        />
      )}

      {showLogoPreview && logoFile && (
        <div className="modal show" style={{ display: 'block', backgroundColor: 'rgba(0,0,0,0.5)' }}>
          <div className="modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">LOGO </h5>
                <button
                  type="button"
                  className="btn-close"
                  onClick={() => setShowLogoPreview(false)}
                ></button>
              </div>
              <div className="modal-body text-center">
                <img
                  src={logoFile}
                  alt="LOGO "
                  style={{
                    maxWidth: '100%',
                    maxHeight: '400px',
                    objectFit: 'contain'
                  }}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  onClick={() => setShowLogoPreview(false)}
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default QuotationGenerator;